* {
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  background-color: teal;
}
.header p{
  margin: 0 auto;
  text-align: center;

}

.container {
  width: 300px;
  height: 480px;
  margin-left: auto;
  margin-right: auto;
  background: lavenderblush;
  box-shadow: 10px 1px 10px 1px;
}

.picture {
  width: 100px;
  height: 100px;
  padding: 5px;
  transform: translate(95px, 45px);
  display: flex;
  border: 3px solid darkblue;
  background-color: darkcyan;
}

.header h2 {
margin: 0;
padding: 0;
font-weight: 700;
font-size: 30px;
align-items: center;
margin: 60px;
justify-content: center;
color: blue;
}
 
.myButton {
  display: flex;
  justify-content: center;
}

button {
  margin: 10px;
  color: black;
  background: lightgreen;
  padding: 20px;
  border: 0;
  font-size: 1.2em;
  border-radius: 10px;
  cursor: pointer;
}
button:hover {
  background: lightblue;
}
.likes {
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.likeCounter img {
  font-size: 10px;
  color:blue;
}
span {
  color: black;
}
.social-media {
  width: 40px;
  height: 70px;
  margin: -18px;
  overflow: hidden;
}

footer {
  display:flex;
  justify-content: center;
}

.social-media:hover {
  background: purple;
  border-radius: 5px;
}
  section {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    border-radius: 10px;
    overflow: hidden;   
}





